@use "../base/variables" as *;
@use "../base/mixins" as *;

.page {
  height: 100%;
  scrollbar-gutter: stable;

  &--nojs {
    .header__toggle-nav {
      @include visually-hidden;
    }

    .header__toggle-theme {
      grid-column: 3;
    }

    @media (max-width: $tablet - 1px) {
      .nav {
        margin-top: 20px;
        position: static;
        display: block;
      }
    }
  }
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  color: var(--primary-text);
  background-color: var(--primary-background);

  &--scroll-lock {
    overflow: hidden;
  }
}

.page__main {
  @media (min-width: $tablet) {
    margin-top: 10px;
  }
}
